.clock {
  width: 30rem;
  height: 30rem;
  border: 2rem solid whitesmoke;
  border-radius: 50%;
  margin: 5vh auto;
  padding: 2rem;
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-box-shadow: 0 0 0 0.2rem rgba(0, 0, 0, 0.1), inset 0 0 0 0.5rem #efefef,
    inset 0 0 0.5rem black, 0 0 0.5rem rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 0 0.2rem rgba(0, 0, 0, 0.1), inset 0 0 0 0.5rem #efefef,
    inset 0 0 0.5rem black, 0 0 0.5rem rgba(0, 0, 0, 0.2);
}

.board {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-transform: translateY(-0.3rem);
      -ms-transform: translateY(-0.3rem);
          transform: translateY(-0.3rem);
  border-radius: 50%;
}

.axis {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  background: goldenrod;
  position: absolute;
  left: 50%;
  top: calc(50% + 0.3rem);
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-box-shadow: inset 0 0 0 0.1rem rgba(256, 256, 256, 0.4),
    inset 0 0 0 0.5rem rgba(0, 0, 0, 0.1);
          box-shadow: inset 0 0 0 0.1rem rgba(256, 256, 256, 0.4),
    inset 0 0 0 0.5rem rgba(0, 0, 0, 0.1);
}



